<template>
  <tr class="table-dark row-submission">
    <td></td>
    <td class="cell">
      <input 
        type="text" 
        class="cell-input form-control form-control-sm" 
        v-model="newNodeX" 
        placeholder="X" 
        oninput="this.value = this.value.replace(/[^0-9.-]/g, '').replace(/(\\..*)\\./g, '$1')"	
      />
    </td>
    <td class="cell">
      <input 
        type="text" 
        class="cell-input form-control form-control-sm" 
        v-model="newNodeY" 
        @keyup.enter="submitNode" 
        placeholder="Y"
        oninput="this.value = this.value.replace(/[^0-9.-]/g, '').replace(/(\\..*)\\./g, '$1')" 
      />
    </td>
    <td class="cell-button">
      <button 
        type="button" 
        class="btn btn-outline-success submit" 
        @click="submitNode" 
      ><b><i class="icon-add"></i></b></button>
    </td>
  </tr>
</template>

<script>
export default {
  name: 'NodeRowAdd',
	props: ['nodes'],
	data () {
		return {
			nextUniqueId: 5,
			newNodeX: '',
			newNodeY: '',
		}
	},
	methods: {
		submitNode() {
			this.$emit('submit', this.newNodeX, this.newNodeY);
			this.newNodeX = '';
			this.newNodeY = '';
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



