<template>
  <div id="canvas-settings-container">

      <transition name="slide-fade">
        <div id="canvas-settings" v-show="config.display">

          <div>Zoom (scroll canvas)</div>
          <div style="grid-column: span 2">
            <input type="range" class="form-control-range" 
                :min="config.scaleMin" :max="config.scaleMax" 
                step="any" v-model.number="config.scale">
          </div>

          <div>Origin offset (drag canvas)</div>
          <div>
            <input type="range" class="form-control-range" 
                min="0" :max="config.canvasW" v-model.number="config.x">
          </div>
          <div>
            <input type="range" class="form-control-range" style="direction: rtl"
              min="0" :max="config.canvasH" v-model.number="config.y"/>
          </div>
          
          <input-checkbox :object="config" :setting="'nodeLabels'" 
              :id="'checkbox-node-labels'" :label="'Node labels'"></input-checkbox>
          <input-checkbox :object="config" :setting="'appliedForces'" 
              :id="'checkbox-applied-forces'" :label="'Nodal loads'"></input-checkbox>
          <input-checkbox :object="config" :setting="'elementLoads'" 
              :id="'checkbox-element-loads'" :label="'Distributed loads'"></input-checkbox>
          <input-checkbox :object="config" :setting="'supports'" 
              :id="'checkbox-supports'" :label="'Supports'"></input-checkbox>
          <input-checkbox :object="config" :setting="'elementForces'" 
              :id="'checkbox-element-forces'" :label="'Axial forces'"></input-checkbox>
          <input-checkbox :object="config" :setting="'reactions'" 
              :id="'checkbox-reactions'" :label="'Reactions'"></input-checkbox>
          <input-checkbox :object="config" :setting="'reactions'" 
              :id="'checkbox-reactions'" :label="'Displacements'"></input-checkbox>
          <input-checkbox :object="config" :setting="'axes'" 
              :id="'checkbox-axes'" :label="'Axes'"></input-checkbox>

          <div style="grid-column: 1 / 2">Precision (1-9)</div>
          <div style="grid-column: span 1;">
            <input 
                type="number" 
                class="form-control form-control-sm" 
                min="1" max="9" 
                v-model.number="config.significantFigures"
            >
          </div>
          
      </div>
    </transition>

    <div id="canvas-header">
      <div>Canvas</div>
      <button 
          class="btn btn-outline-dark" 
          @click="config.display=!config.display"
      >&#x2699;</button>
    </div>

  </div>
</template>

<script>
import InputCheckbox from './InputCheckbox'

export default {
  name: 'CanvasSettings',
  components: {
    InputCheckbox,
  },
  props: ['config'],
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#canvas-settings {
	font-size: 0.875rem;
	background-color: #21252988;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 0.75rem 0.5rem;
	padding: 0.5rem 1rem;
  z-index: 1;
}

#canvas-header {
	align-items: center; 
	background-color: #212529;
	margin: 0;
	display: flex; 
	font-weight: 700;
	justify-content: space-between; 
	padding: 0.5rem 0.5rem 0.5rem 1rem;
  z-index: 1;
  button {
      border: none; 
      color: white; 
      font-weight: 700;
  }
}

</style>



