<template>
  <svg>
    <defs>
      <marker 
					id="arrow" 
					orient="auto"
					markerWidth="6" markerHeight="20"
					refX="3" refY="0">
        <path 
						d="M3,0 L3,10" 
						class="load-arrow"
						marker-end="url(#head)"
				/>
      </marker>
    </defs>

    <path 
				:id="'path' + element.id" 
				class="path-element" 
				:d="elementPathDefinition"
		/>
    <path 
				:id="'pathw' + element.id" 
				v-show="element.w !== 0 && view.elementLoads" 
				class="path-element-load" 
				:d="wPathDefinition"
		/>

    <circle 
				class="circle-release" 
				v-show="!element.startRigid" 
				:cx="xStartOffset" 
				:cy="yStartOffset" 
				r="2"
		/>
    <circle 
				class="circle-release" 
				v-show="!element.endRigid" 
				:cx="xEndOffset" 
				:cy="yEndOffset" 
				r="2"
		/>
    <text 
				class="select-all"
				:transform="transformList" 
				v-show="view.elementForces"
		>
      <textPath 
					:href="'#path' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan dy="-.25em">{{ forceRounded }}</tspan>
      </textPath>
    </text>
    <text 
				class="select-all"
				v-show="element.w !== 0 && view.elementLoads">
      <textPath 
					:href="'#pathw' + element.id" 
					startOffset="50%" 
					text-anchor="middle"
			>
        <tspan class="load-text" dy="-0.25em">{{ Math.abs(element.w) }}</tspan>
      </textPath>
    </text>
  </svg>
</template>

<script>
import math from 'mathjs'

export default {
  name: 'ElementPath',
	props: ['element', 'element-force', 'view'],
	computed: {
		l () {
			return Math.sqrt((this.xEnd - this.xStart) * (this.xEnd - this.xStart) + 
					(this.yEnd - this.yStart) * (this.yEnd - this.yStart));
		},
		xStart () {
			return this.view.x + this.view.scale * this.element.start.x;
		},
		yStart () {
			return this.view.y - this.view.scale * this.element.start.y;
		},
		xEnd () {
			return this.view.x + this.view.scale * this.element.end.x;
		},
		yEnd () {
			return this.view.y - this.view.scale * this.element.end.y;
		},
		elementPathDefinition() {
			return 'M' + (this.xStart) + ',' + (this.yStart) 
					+ 'L' + (this.xEnd) + ',' + (this.yEnd);
		},
		wPathDefinition() {
			let str = 'm' + 12 * (-this.yStart + this.yEnd) / this.l * Math.sign(this.element.w) + ',' + 13 * (-this.xEnd + this.xStart) / this.l * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w)
					+ 'l' + (this.xEnd - this.xStart) / 3 * Math.sign(this.element.w) + ',' + (this.yEnd - this.yStart) / 3 * Math.sign(this.element.w);
			if (Math.sign(this.element.w) === 1) {
				return 'M' + (this.xStart) + ',' + (this.yStart) + str;
			} else {
				return 'M' + (this.xEnd) + ',' + (this.yEnd) + str;
			}
		},
		forceRounded () {
			return (math.equal(this.elementForce, 0)) ? 0 
					: this.elementForce.toPrecision(this.view.significantFigures);
		},	
		topRotate () {
			return (this.element.start.x < this.element.end.x) ? 0 : 180;
		},
		xMid () {
			return (this.xStart + this.xEnd) / 2;
		},
		yMid () {
			return (this.yStart + this.yEnd) / 2;
		},
		xStartOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.125;
		},
		yStartOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.125;
		},
		xEndOffset () {
			return this.xStart + (this.xEnd - this.xStart) * 0.875;
		},
		yEndOffset () {
			return this.yStart + (this.yEnd - this.yStart) * 0.875;
		},
		transformList () {
			return "rotate(" + this.topRotate + ' ' + this.xMid + ' ' + this.yMid + ')';
		},
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/_variables.scss";

.path-element {
	stroke: $dark-warm;
	stroke-width: 2px; 
}

.path-element-load {
	marker-end: url(#arrow);
	marker-mid: url(#arrow);
	stroke: $loads-of-gray; 
	stroke-width: 1px; 
}
.circle-release {
	fill: #fff;
	stroke: $dark-warm;
}
.load-arrow {
	stroke: $loads-of-gray; 
	fill: none;
}
.load-text {
	fill: $loads-of-gray; 
}
</style>



