<template>
  <svg>
    <defs>
      <marker 
					id="head" 
					orient="auto"
        	markerWidth="6" markerHeight="6"
        	refX="2.5" refY="3">
        <path class="load-arrow" d="M1,1 L3,3 L1,5"/>
      </marker>
    </defs>

    <circle 
			class="circle-node" 
			:cx="x" 
			:cy="y" 
			r="1.5"
		/>
    
    <circle 
			class="circle-node" 
			v-show="view.nodeLabels" 
			:cx="x" 
			:cy="y" 
			r="5" 
			style="fill: #000;"
		/>

    <text 
			v-show="view.nodeLabels" 
			:x="x" 
			:y="y + 3.5" 
			text-anchor="middle" 
			style="fill: #fff; font-size: 0.625rem"
		>{{ node.n }}</text>
    
    <path 
      class="path-force"
      v-show="view.appliedForces && f !== 0"
      :d="forcePathDefinition" 
    />
    <text 
			class="load-text select-all"
      v-show="view.appliedForces && f !== 0" 
      :x="forceTextX" :y="forceTextY" 
      text-anchor="middle"
    >{{ fRounded }}</text>

    <path 
      class="path-force"
      v-show="view.appliedForces && mZ !== 0"
      :d="momentPathDefinition"
      style="fill: none;"
    />
    <text 
			class="load-text select-all"
      v-show="view.appliedForces && mZ !== 0" 
      :x="x + 13" :y="y - 13" 
      style="fill: #947Ebc"
    >{{ mZRounded }}</text>

    <polygon 
				class="support fx" 
				v-show="node.rx && view.supports" 
        :points="reactionXTriangle" 
		/>
    <text 
				class="fx select-all"
				v-show="view.reactions && node.rx" 
				:x="x - 20" :y="y + 3" 
				text-anchor="end" 
				fill=""
		>{{ rxRounded }}</text>
		
    <polygon 
				class="support fy" 
				v-show="node.ry && view.supports" 
        :points="reactionYTriangle" 
		/>
    <text 
				class="fy select-all"
				v-show="view.reactions && node.ry" 
				:x="x" :y="y + 25" 
				text-anchor="middle" 
				fill=""
		>{{ ryRounded }}</text>
    <polygon 
				class="support mz" 
				v-show="node.rz && view.supports" 
        :points="reactionZTriangle" 
		/>
    <text 
				class="mz select-all"
				v-show="view.reactions && node.rz" 
				:x="x - 20" 
				:y="y + 25" 
				text-anchor="end" 
				fill=""
		>{{ rzRounded }}</text>
  </svg>
</template>

<script>
import math from 'mathjs'

export default {
  name: 'NodeCircle',
	props: ['node', 'reactions', 'view'],
	computed: {
		dx () {
			return (this.node.x === "") ? 0 : this.node.x * this.view.scale;
		},
		dy () {
			return (this.node.y === "") ? 0 : this.node.y * this.view.scale;
		},
		x () {
			return this.view.x + this.dx;
		},
		y () {
			return this.view.y - this.dy;
		},
		fX () {
			return (this.node.loadX === "") ? 0 : this.node.loadX;
		},
		fY () {
			return (this.node.loadY === "") ? 0 : this.node.loadY;
		},
		mZ () {
			return (this.node.loadZ === "") ? 0 : this.node.loadZ;
		},
		f () {
			return math.sqrt(Math.pow(this.fX, 2) + Math.pow(this.fY, 2));
		},
		fRounded() {
			return this.f.toPrecision(this.view.significantFigures);
		},
		mZRounded () {
			return Math.abs(this.mZ.toPrecision(this.view.significantFigures));
		},
		rxRounded() {
			return (math.equal(this.reactions.fx, 0)) ? 0 
					: this.reactions.fx.toPrecision(this.view.significantFigures);
		},
		ryRounded() {
			return (math.equal(this.reactions.fy, 0)) ? 0 
					: this.reactions.fy.toPrecision(this.view.significantFigures);
		},
		rzRounded() {
			return (math.equal(this.reactions.mz, 0)) ? 0 
					: this.reactions.mz.toPrecision(this.view.significantFigures);
		},
		reactionXTriangle() {		
			return (this.x - 6) + ',' + (this.y) + ' ' 
					+ (this.x - 11) + ',' + (this.y - 4) + ' ' 
					+ (this.x - 11) + ',' + (this.y + 4);
		},
		reactionYTriangle() {
			return (this.x) + ',' + (this.y + 6) + ' ' 
					+ (this.x + 4) + ',' + (this.y + 11) + ' ' 
					+ (this.x - 4) + ',' + (this.y + 11);
		},
		reactionZTriangle() {
			return (this.x - 4) + ',' + (this.y + 4) + ' ' 
					+ (this.x - 10.3) + ',' + (this.y + 4.7) + ' ' 
					+ (this.x - 4.7) + ',' + (this.y + 10.3);
		},
		forcePathDefinition() {
			if (this.f === 0) return "";
			return 'M' + (this.x - 50 * this.fX / this.f) + ','
					+ (this.y + 50 * this.fY / this.f) + 'L' 
					+ (this.x - 10 * this.fX / this.f) + ',' 
					+ (this.y + 10 * this.fY / this.f);
		},
		momentPathDefinition() {
			if (this.mZ === 0) return "";
			return 'M' + (this.x - 7.5) + ',' + (this.y + 13 * math.sign(this.mZ)) 
					+ 'A 15 15 0 1 ' + ((math.sign(this.mZ) - 1) / -2)
					+ (this.x - 7.5) + ',' + (this.y - 13 * math.sign(this.mZ));
		},
		forceTextX() {
			if (this.f === 0) return 0;
			return this.x - 55 * this.fX / this.f;
		},
		forceTextY() {
			if (this.f === 0) return 0;
			return this.y + 55 * this.fY / this.f;
		}
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/_variables.scss";

.fx {
	fill: #B42E09;

}
.fy {
	fill: #52A23A;
}

.mz {
	fill: #4167B7;
}

.circle-node {
	fill: $dark-warm;
}

.support {
	opacity: 0.8;
}

.path-force {
	marker-end: url(#head);
	stroke: $loads-of-gray;
	stroke-width: 2px;
}
.load-arrow {
	stroke: $loads-of-gray;
	fill: none;
}
.load-text {
	fill: $loads-of-gray;
}
</style>



